import React,{useState} from "react";
import { Paper, TextField, Button, Grid } from "@mui/material";
import "./Contact.css"; // Import specific styles if needed
import SendIcon from "@mui/icons-material/Send";
import axios from 'axios';
import toastr from 'toastr';
import 'toastr/build/toastr.min.css';

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  });
  const [loading, setLoading] = useState(false);
  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.name || !formData.email || !formData.subject || !formData.message) {
      toastr.error('Please fill in all fields');
      return;
    }

    try {
      setLoading(true);
      const response = await axios.post('http://localhost:3001/send-email', formData);
      if (response.status === 200) {
        toastr.success('Email sent successfully!');
        setFormData({
          name: '',
          email: '',
          subject: '',
          message: ''
        });
      }
    } catch (error) {
      toastr.error('Failed to send email. Please try again later.');
      console.error('Error sending email:', error);
    } finally {
      setLoading(false);
    }
  };
  return (
    <div className="section" data-aos="fade-up" style={{ padding: "20px" }}>
      <Grid container spacing={3} justifyContent="center">
        <Grid
          item
          xs={12}
          md={5}
          style={{ paddingLeft: "0%", marginTop: "15%" }}
        >
          <h1>Get in Touch!</h1>
          <h3>Feel free to reach out for any inquiries or collaborations.</h3>
        </Grid>

        <Grid item xs={12} md={5}>
          <Paper
            elevation={3}
            style={{
              padding: "20px",
              borderRadius: "15px",
              marginBottom: "16%",
            }}
          >
            <h1>Send a message</h1>
            <form
              style={{
                display: "flex",
                flexDirection: "column",
                width: "100%",
              }}
              onSubmit={handleSubmit}
            >
              <TextField
                label="Full Name"
                variant="outlined"
                margin="normal"
                fullWidth
                required
                name="name"
                value={formData.name}
                onChange={handleChange}
                sx={{
                  "& .MuiOutlinedInput-root": {
                    borderRadius: "15px",
                    "&.Mui-focused fieldset": {
                      borderColor: "#7a38c8", // Set focus color for the border
                    },
                  },
                  "& .MuiInputLabel-root": {
                    color: "grey", // Default label color
                    "&.Mui-focused": {
                      color: "#7a38c8", // Set label color when focused
                    },
                  },
                }}
              />
              <TextField
                label="Email"
                type="email"
                variant="outlined"
                margin="normal"
                fullWidth
                required
                name="email"
                value={formData.email}
                onChange={handleChange}
                sx={{
                  "& .MuiOutlinedInput-root": {
                    borderRadius: "15px",
                    "&.Mui-focused fieldset": {
                      borderColor: "#7a38c8", // Set focus color for the border
                    },
                  },
                  "& .MuiInputLabel-root": {
                    color: "grey", // Default label color
                    "&.Mui-focused": {
                      color: "#7a38c8", // Set label color when focused
                    },
                  },
                }}
              />
              <TextField
                label="Message"
                variant="outlined"
                margin="normal"
                fullWidth
                multiline
                rows={4}
                name="message"
              value={formData.message}
              onChange={handleChange}
                required
                sx={{
                  "& .MuiOutlinedInput-root": {
                    borderRadius: "15px",
                    "&.Mui-focused fieldset": {
                      borderColor: "#7a38c8", // Set focus color for the border
                    },
                  },
                  "& .MuiInputLabel-root": {
                    color: "grey", // Default label color
                    "&.Mui-focused": {
                      color: "#7a38c8", // Set label color when focused
                    },
                  },
                }}
              />
              <Button
                type="submit"
                variant="contained"
                style={{
                  marginTop: "20px",
                  backgroundColor: "#7a38c8",
                  color: "#fff",
                  borderRadius: "10px",
                  width: "200px", // Set a specific width here
                  "&:hover": {
                    backgroundColor: "#6a2cb8",
                  },
                }}
                endIcon={<SendIcon />} // Adding the icon to the button
                disabled={loading}
              >
                {loading ? 'Sending...' : 'Send Message'}
              </Button>
            </form>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default Contact;
