import React, { useEffect } from "react";
import { Grid, Paper, Box } from "@mui/material";
import AOS from "aos";
import "aos/dist/aos.css";
import "./Skills.css"; // Import specific styles if needed
import {
  Code,
  Storage,
  AdminPanelSettings,
  Web,
  DeviceHub,
  School,
} from "@mui/icons-material";

const skillsData = [
  {
    title: "Web Development",
    description: `Hi there! I'm Bishwas Shrestha, a passionate web developer with a diverse skill set and a love for creating innovative digital experiences. With several years of experience in the field, I have worked extensively with technologies such as Laravel, PHP, JavaScript, Node.js, React, and React Native.`,
    icon: <Code />,
  },
  {
    title: "Website Development",
    description: `On the frontend, I am proficient in building responsive and user-friendly interfaces using frameworks like Bootstrap and Tailwind CSS. I also have experience integrating third-party APIs and services to enhance the functionality of web applications.`,
    icon: <Web />,
  },
  {
    title: "Database Management",
    description: `I am proficient in database management systems, including MySQL, and have experience setting up and maintaining databases for various web applications.`,
    icon: <Storage />,
  },
  {
    title: "Server Administration",
    description: `Additionally, I have hands-on experience with server administration tasks, including managing cPanel accounts, using FileZilla for file transfers, and working with Linux-based servers.`,
    icon: <AdminPanelSettings />,
  },
  {
    title: "IoT and MQTT",
    description: `In addition to web development, I have a keen interest in IoT (Internet of Things) technologies and have worked with MQTT (Message Queuing Telemetry Transport) protocols to develop smart applications and devices.`,
    icon: <DeviceHub />,
  },
  {
    title: "Continuous Learning",
    description: `I am passionate about staying up-to-date with the latest trends and advancements in the world of technology. I regularly participate in online courses, workshops, and hackathons to expand my skill set and deepen my knowledge.`,
    icon: <School />,
  },
];

const Skills = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
    AOS.refresh();
  }, []);
  return (
    <div className="section" data-aos="fade-up">
      <div className="container" style={{ padding: "30px", marginTop: "0px" }}>
        <Grid container spacing={4} alignItems="center">
          {/* Left Column for Title and Description */}
          <Grid item xs={12} md={6}>
            <div className="skills-body-contents">
            <h1 variant="h1" align="left">
              Skills and Experiences
            </h1>
            <p
              align="left"
              style={{ fontSize: "16px", fontWeight: 350, lineHeight: 1.8 }}
            >
              I am proficient in a variety of technologies that allow me <br />
              to build innovative and dynamic web applications.
            </p>
            <Grid
              container
              spacing={4}
              justifyContent="left"
              style={{ marginTop: "20px" }}
            >
              <Grid item>
                <i className="fab fa-html5 fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-css3-alt fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-js fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-php fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-laravel fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-linux fa-2x"></i>
              </Grid>
              <Grid item>
                <i className="fab fa-mysql fa-2x"></i>
              </Grid>
            </Grid>
            </div>

          </Grid>

          {/* Right Column for Skills */}
          <Grid item xs={12} md={6}>
            <Grid container spacing={2}>
              {skillsData.map((skill, index) => (
                <Grid item xs={12} sm={6} key={index}>
                  <Paper elevation={3} className="skill-card">
                    <Box display="flex" alignItems="center" padding={2}>
                      <Box marginRight={2}>{skill.icon}</Box>
                      <div>
                        <h3>{skill.title}</h3>
                      </div>
                    </Box>
                  </Paper>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default Skills;
