import React, { useEffect, useRef } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { ParallaxProvider } from 'react-scroll-parallax';
import Home from './home/Home';
import Skills from './skills/Skills';
import Projects from './projects/Projects';
import Contact from './contact/Contact';
import Music from './music/Music';
import '../styles/Portfolio.css'; // Add custom styles here
import HomeIcon from '@mui/icons-material/Home';
import BuildIcon from '@mui/icons-material/Build';
import WorkIcon from '@mui/icons-material/Work';
import EmailIcon from '@mui/icons-material/Email';
import MusicNoteIcon from '@mui/icons-material/MusicNote';

const Portfolio = () => {
  const containerRef = useRef(null);
  const homeRef = useRef(null);
  const skillsRef = useRef(null);
  const projectsRef = useRef(null);
  const contactRef = useRef(null);
  const musicRef = useRef(null);

  useEffect(() => {
    AOS.init({ duration: 1200 });
    
    const handleScroll = (event) => {
      if (containerRef.current) {
        containerRef.current.scrollLeft += event.deltaY * 0.4;
      }
      AOS.refresh(); // Refresh AOS on scroll
    };

    const container = containerRef.current;
    if (container) {
      container.addEventListener('wheel', handleScroll, { passive: false });
    }

    return () => {
      if (container) {
        container.removeEventListener('wheel', handleScroll);
      }
    };
  }, []);

  // Scroll to the specific section
  const scrollToSection = (ref) => {
    if (containerRef.current && ref.current) {
      containerRef.current.scrollTo({
        left: ref.current.offsetLeft,
        behavior: 'smooth',
      });
    }
  };

  return (
    <ParallaxProvider>
      <div className="scroll-container" ref={containerRef}>
        {/* Section references */}
        <div ref={homeRef}>
          <Home />
        </div>
        <div ref={skillsRef}>
          <Skills />
        </div>
        <div ref={projectsRef}>
          <Projects />
        </div>
        <div ref={contactRef}>
          <Contact />
        </div>
        <div ref={musicRef}>
          <Music />
        </div>
      </div>

      {/* Fixed bottom navigation */}
      <nav className="bottom-nav">
        <ul>
          <li onClick={() => scrollToSection(homeRef)}>
            <HomeIcon fontSize="large" />
          </li>
          <li onClick={() => scrollToSection(skillsRef)}>
            <BuildIcon fontSize="large" />
          </li>
          <li onClick={() => scrollToSection(projectsRef)}>
            <WorkIcon fontSize="large" />
          </li>
          <li onClick={() => scrollToSection(contactRef)}>
            <EmailIcon fontSize="large" />
          </li>
          <li onClick={() => scrollToSection(musicRef)}>
            <MusicNoteIcon fontSize="large" />
          </li>
        </ul>
      </nav>
    </ParallaxProvider>
  );
};

export default Portfolio;
