import React, { useEffect, useState } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './Projects.css'; // Ensure to import your CSS file
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import Grid from '@mui/material/Grid'; // Import MUI Grid
import Skeleton from '@mui/material/Skeleton'; // Import Skeleton
import Button from "@mui/material/Button";
import { FaGithub } from 'react-icons/fa';

const itemData = [
  {
    img: 'assets/1.jpg',
    title: 'Inventory Management System',
    link: 'https://github.com/developwithsushan/ims',
  },
  {
    img: 'assets/2.jpg',
    title: 'React Portfolio',
    link: 'https://github.com/BishwasGit/React.Portfolio',
  },
  {
    img: 'assets/1.jpg',
    title: 'Vehicle App',
    link: 'https://github.com/BishwasGit/VechicleGuardian',
  },
  {
    img: 'assets/2.jpg',
    title: 'QR App',
    link: 'https://github.com/BishwasGit/QRIdentity',
  },
  {
    img: 'assets/1.jpg',
    title: 'Maze Game',
    link: 'https://github.com/BishwasGit/MazeEscape',
  },
  {
    img: 'assets/1.jpg',
    title: 'Guide Website',
    link: 'https://github.com/BishwasGit/User-Guide-CMS',
  },
  {
    img: 'assets/2.jpg',
    title: 'Manufacturing Reporting',
    link: 'https://github.com/BishwasGit/Daily-Manufacturing-Reporting',
  },
  {
    img: 'assets/1.jpg',
    title: 'Online Voting',
    link: 'https://github.com/BishwasGit/Online-Voting',
  },
  {
    img: 'assets/2.jpg',
    title: 'Project 9',
    link: 'https://github.com/BishwasGit/project9',
  },
  {
    img: 'assets/1.jpg',
    title: 'Business Website',
    link: 'https://github.com/BishwasGit/PORTO',
  },
  {
    img: 'assets/1.jpg',
    title: 'Tower Defense Game',
    link: 'https://github.com/BishwasGit/Tower-Defense',
  },
  {
    img: 'assets/2.jpg',
    title: 'Javascript Game',
    link: 'https://github.com/BishwasGit/Caveescape',
  },
  {
    img: 'assets/1.jpg',
    title: 'Javascript Game',
    link: 'https://github.com/BishwasGit/Caveescape',
  },
  {
    img: 'assets/2.jpg',
    title: 'Javascript Game',
    link: 'https://github.com/BishwasGit/Caveescape',
  },
  {
    img: 'assets/1.jpg',
    title: 'Javascript Game',
    link: 'https://github.com/BishwasGit/Caveescape',
  },
];

const Projects = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
    AOS.refresh(); 
  }, []);
  const [loading, setLoading] = useState(true);
  return (
    <div className="section" data-aos="fade-up">
      <Grid container spacing={2}>
        {/* Heading Column */}
        <Grid item xs={12} md={6} style={{ paddingLeft: '10%' }}>
          <h1 style={{ marginTop: '40%' }}>Projects</h1>
          <h3>These are the thumbnail of my projects with its repo link.</h3>
          <h3>Full projects are available on my GitHub</h3>
          <a
            href="https://github.com/BishwasGit"
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: 'underline' }}
          >
            <Button
              variant="outlined"
              color="primary"
              startIcon={ <FaGithub />}
              sx={{
                borderColor: 'black',
                color: 'black', // Corrected this line
                '&:hover': {
                  backgroundColor: 'black',
                  borderColor: 'black', // Keep border color on hover
                  color: '#ffffff', // Change text color on hover (optional)
                },
                marginTop : '20px'
              }}
            >
            Link to GitHub
            </Button>
          </a>
        </Grid>
        {/* Image List Column */}
        <Grid item xs={12} md={6} className="image-list-container">
  <div className="projects-scroll-container">
    <ImageList sx={{ width: '100%', height: 'auto' }} cols={3} rowHeight="auto">
      {itemData.map((item) => (
        <ImageListItem key={item.img + Math.random()}> {/* Ensure unique keys */}
          <a
            href={item.link}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              position: 'relative',
              display: 'block',
              overflow: 'hidden',
            }}
          >
            {loading && (
              <Skeleton variant="rectangular" width="100%" height="100%">
                {/* Placeholder for the image while loading */}
              </Skeleton>
            )}
            <img
              src={`${item.img}?w=250&h=250&fit=crop&auto=format`}
              alt={item.title}
              loading="lazy"
              style={{
                width: '100%',
                height: '100%',
                objectFit: 'cover',
                transition: 'filter 0.3s ease',
                display: loading ? 'none' : 'block', // Hide image while loading
              }}
              onLoad={() => setLoading(false)} // Set loading to false when image loads
              className="image"
            />
          </a>
        </ImageListItem>
      ))}
    </ImageList>
  </div>
</Grid>

      </Grid>
    </div>
  );
};

export default Projects;
