import React, { useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { Parallax } from "react-scroll-parallax";
import "./Home.css";
import DownloadIcon from "@mui/icons-material/Download";
import Button from "@mui/material/Button";
import { ReactTyped } from "react-typed";
import SocialMediaIcons from "./SocialMediaIcons";

const Home = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
    AOS.refresh(); 
  }, []);
  return (
    <div className="section" data-aos="fade-up">
      <Parallax speed={-10}>
        <div className="home-content">
          <div className="intro">
            <h1>
              Hello, I am
              <br />
              Bishwas Shrestha
            </h1>
            <h2>
              <ReactTyped
                strings={[
                  "Web Developer",
                  "Web Designer",
                  "Frontend Developer",
                  "Backend Developer",
                  "Fullstack Developer",
                  "Student",
                ]}
                typeSpeed={40}
                backSpeed={50}
                loop
                style={{ whiteSpace: "nowrap" }}
              />
            </h2>
            <p style={{fontSize:'20px',fontWeight:350,lineHeight:1.8}}>
              I’m currently working as both Frontend and Backend Web Developer.
              <br />
              I’m looking to collaborate on MERN, Laravel, WordPress, and
              React-Native Projects.
            </p>
            <a href="/assets/cv.pdf" className="btn" download>
            <Button
              variant="outlined"
              color="primary"
              startIcon={<DownloadIcon />}
              sx={{
                borderColor: 'black',
                color: 'black', // Corrected this line
                '&:hover': {
                  backgroundColor: 'black',
                  borderColor: 'black', // Keep border color on hover
                  color: '#ffffff', // Change text color on hover (optional)
                },
                marginTop : '20px'
              }}
            >
              Download CV
            </Button>
            </a>
          <SocialMediaIcons />
          </div>
          <div className="home-image">
            <img
              src="https://i.giphy.com/media/v1.Y2lkPTc5MGI3NjExZ3d5OHpja3Z4MDk0MXZjNXZna2Vld2doOTczYW81eW9ra2hmb2RwMSZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/nVE8OaIGkUhf7rkieR/giphy.gif"
              alt="Right pointing arrow"
              style={{ width: '80%', marginLeft: '6%', animation: 'bounce 2s infinite' }} 
            />
          </div>
        </div>
      </Parallax>
    </div>
  );
};

export default Home;
