import React from 'react';
import { Grid } from '@mui/material';
import SpotifyLogo from './spotify.png'; // Ensure you have the Spotify logo image

const Music = () => {
  const playlistId = '52brjFysydS1GF3NmJF7fm'; // Your Spotify playlist ID

  return (
    <div className="section" data-aos="fade-up">
      <Grid container spacing={1} alignItems="left">
        {/* Left column: Logo and description */}
        <Grid item xs={12} md={6} container direction="column" alignItems="left" style={{ paddingLeft: '10%',marginTop:'4%' }}>
        <div style={{ backgroundColor: 'black', padding: '20px', borderRadius: '12px', marginBottom: '20px',maxWidth:'150px' }}>
            <img
              src={SpotifyLogo}
              alt="Spotify Logo"
              style={{ width: '150px' }}
            />
          </div>
          <h1 variant="h5" component="h2" color="primary" align="left">
            Enjoy My Favorite Playlist
          </h1>
          <h3 variant="body1" color="textSecondary" align="left">
            Explore some of the tracks that inspire and motivate me.<br/> Click play and vibe along!
          </h3>
        </Grid>

        {/* Right column: Spotify player */}
        <Grid item xs={12} md={6}>
          <iframe
            title="Spotify Embed: Recommendation Playlist"
            src={`https://open.spotify.com/embed/playlist/${playlistId}?utm_source=generator&theme=0`}
            width="100%"
            height="360"
            frameBorder="0"
            allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture"
            loading="lazy"
            style={{ borderRadius: '12px', border: 'none' }}
          />
        </Grid>
      </Grid>
    </div>
  );
};

export default Music;
